'use client';

import Image from 'next/image';
import { menuCategories } from '@/data/menu-data';

interface CategoryCardsProps {
  onCategoryClick: (categoryId: string) => void;
}

export default function CategoryCards({ onCategoryClick }: CategoryCardsProps) {
  // Display first 2 categories
  const selectedCategories = menuCategories.slice(0, 2);

  return (
    <div className="flex flex-col gap-6 h-full">
      {selectedCategories.map((category) => (
        <button
          key={category.id}
          onClick={() => onCategoryClick(category.id)}
          className="flex-1 relative overflow-hidden rounded-lg shadow-lg hover:shadow-xl transition-all hover:scale-105 group cursor-pointer bg-gray-900"
        >
          {/* Background Image */}
          <div className="absolute inset-0 overflow-hidden">
            <Image
              src={
                category.id === 'signature'
                  ? '/paneer-tikka-masala.png'
                  : '/peanut-butter-strawberry-smoothie.jpg'
              }
              alt={category.name}
              fill
              className="object-cover group-hover:scale-110 transition-transform duration-300"
            />
          </div>

          {/* Overlay */}
          <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/40 to-transparent" />

          {/* Content */}
          <div className="absolute inset-0 flex flex-col justify-end p-6">
            <h3 className="text-2xl font-bold text-white text-balance">
              {category.name}
            </h3>
            <p className="text-amber-300 text-sm mt-2 font-semibold">
              {/* Explore Menu */}
            </p>
          </div>
        </button>
      ))}
    </div>
  );
}
