'use client';

import { useState, useEffect } from 'react';
import Image from 'next/image';
import { ChevronLeft, ChevronRight } from 'lucide-react';

interface CarouselItem {
  id: string;
  title: string;
  image: string;
}

const newAdditions: CarouselItem[] = [
  {
    id: 'new-1',
    title: 'Sizzling Brownie',
    image: '/sizzling-brownie.jpg',
  },
  {
    id: 'new-2',
    title: 'Strawberry Falooda',
    image: '/strawberry-falooda.jpg',
  },
  {
    id: 'new-3',
    title: 'Paneer Tikka Masala',
    image: '/paneer-tikka-masala.png',
  },
];

export default function CarouselSection() {
  const [currentIndex, setCurrentIndex] = useState(0);

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentIndex((prev) => (prev + 1) % newAdditions.length);
    }, 5000);
    return () => clearInterval(timer);
  }, []);

  const handlePrev = () => {
    setCurrentIndex((prev) => (prev - 1 + newAdditions.length) % newAdditions.length);
  };

  const handleNext = () => {
    setCurrentIndex((prev) => (prev + 1) % newAdditions.length);
  };

  const currentItem = newAdditions[currentIndex];

  return (
    <div className="flex flex-col h-full bg-gradient-to-b from-gray-50 to-gray-100 rounded-lg overflow-hidden shadow-lg">
      {/* Carousel Container */}
      <div className="flex-1 relative overflow-hidden bg-black">
        <Image
          src={currentItem.image || "/placeholder.svg"}
          alt={currentItem.title}
          fill
          className="object-cover"
        />
        
        {/* Navigation Buttons */}
        <button
          onClick={handlePrev}
          className="absolute left-4 top-1/2 transform -translate-y-1/2 bg-black/50 hover:bg-black/75 text-white p-2 rounded-full transition-all z-10"
          aria-label="Previous item"
        >
          <ChevronLeft size={24} />
        </button>
        <button
          onClick={handleNext}
          className="absolute right-4 top-1/2 transform -translate-y-1/2 bg-black/50 hover:bg-black/75 text-white p-2 rounded-full transition-all z-10"
          aria-label="Next item"
        >
          <ChevronRight size={24} />
        </button>

        {/* Dot Indicators */}
        <div className="absolute bottom-4 left-1/2 transform -translate-x-1/2 flex gap-2 z-10">
          {newAdditions.map((_, index) => (
            <button
              key={index}
              onClick={() => setCurrentIndex(index)}
              className={`w-2 h-2 rounded-full transition-all ${
                index === currentIndex ? 'bg-white w-8' : 'bg-white/50'
              }`}
              aria-label={`Go to slide ${index + 1}`}
            />
          ))}
        </div>
      </div>

      {/* Title Section */}
      <div className="bg-gray-800 text-white p-6 text-center">
        <h3 className="text-2xl font-bold">{currentItem.title}</h3>
        <p className="text-sm text-gray-300 mt-1">New Addition</p>
      </div>
    </div>
  );
}
